import React from "react";
import { useState } from 'react';
import { QrReader } from 'react-qr-reader';
import { Button } from '@chakra-ui/react';
import { Text } from '@chakra-ui/react';
import { Heading } from '@chakra-ui/react'
import { Card, CardHeader, CardBody } from '@chakra-ui/react'
import { Box } from '@chakra-ui/react'
import { Stack } from '@chakra-ui/react'
import { Radio, RadioGroup } from '@chakra-ui/react'
import { Container } from '@chakra-ui/react'
import {
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableContainer,
} from '@chakra-ui/react'
import tables from './data/tables.json';
import menus from './data/menus.json';

function App () {
  const [data, setData] = useState(null);
  const [showCamera, setShowCamera] = useState(true);
  const [showMenu, setShowMenu] = useState(false);
  const [showOrder, setShowOrder] = useState(false);
  const [order, setOrder] = useState({})

  const setShowMenuToTrue = () => {
    setShowMenu(true);
    setShowCamera(false);
  }

  const setShowOrderToTrue = () => {
    setShowOrder(true);
    setShowMenu(false);
    console.log(order);
  }

  const updateOrder = (category, value) => {
    setOrder({...order, [category]: value})
  }

  return (
    <Container>
      <Card>
        {showCamera && (
          <>
            <QrReader
              constraints={{facingMode: 'user'}}
              onResult={(result, error) => {
                if (!!result) {
                  setData(result?.text);
                }
              }}
            />
          </>
        )}
        {showCamera && data && (
          <div style={{textAlign: 'center'}}>
            <Text fontSize='2xl'>Restaurante</Text>
            <Text fontSize='lg'>{tables[data].name}</Text>
            <Text fontSize='2xl'>Sede</Text>
            <Text fontSize='lg'>{tables[data].campus}</Text>
            <Text fontSize='2xl'>Mesa</Text>
            <Text fontSize='lg'>{tables[data].table}</Text>
            <Button onClick={setShowMenuToTrue}>Confirmar mesa</Button>
          </div>
        )}
        {showMenu && (
          <>
            <Text fontSize='2xl'>Menu</Text>
            {menus[tables[data].name].map(item => {
              return <Card style={{margin: '10px'}}>
                <CardHeader>
                  <Heading size='md'>{item.name}</Heading>
                </CardHeader>
                <CardBody>
                  <Box>
                    <RadioGroup name={item.name} onChange={(value) => updateOrder(item.name, value)}>
                      <Stack>
                        {item.choices.map(choice => {
                          return <Radio value={choice.name}>{choice.name}</Radio>
                        })}
                      </Stack>
                    </RadioGroup>
                  </Box>
                </CardBody>
              </Card>
            })}
            <Button onClick={setShowOrderToTrue}>Pedir</Button>
          </>
        )}
        {showOrder && (
          <TableContainer>
            <Table variant='simple'>
              <Thead>
                <Tr>
                  <Th>Categoria</Th>
                  <Th>Seleccionado</Th>
                </Tr>
              </Thead>
              <Tbody>
                {Object.keys(order).map(key => {
                  return <Tr>
                  <Td><strong>{key}</strong></Td>
                  <Td>{order[key]}</Td>
                </Tr>
                })}
              </Tbody>
            </Table>
          </TableContainer>
        )}
      </Card>
    </Container>
  );
}

export default App;
